<template>
  <div>
    <Head title="个人中心" :show="true"></Head>
    <div v-if="isLogin">
      用户名={{username}} //  {{userInfo.username}}
      token={{userInfo.token}}
      <my-upload></my-upload>
    </div>
    <div v-else>
       <van-button @click="gotoLogin" type="primary">点击登录</van-button>
    </div>
    
  </div>
</template>
<script>
import myUpload from "@/components/Upload.vue"
import {mapState} from 'vuex'
export default {
  computed:{
    ...mapState(['userInfo'])
  },
  data(){
    return {
      isLogin:false,
      username:""
    }
  },
  components:{
    myUpload
  },
  mounted(){
    if(sessionStorage.userInfo){
      this.isLogin = JSON.parse(sessionStorage.userInfo).token;
      this.username = JSON.parse(sessionStorage.userInfo).username;
    }
  },
  methods:{
    gotoLogin(){
      this.$router.push({name:"login"});
    }
  }
}
</script>