<template>
  <div class="imgWrap">
    <img :src="src" alt @click="uploadImg" />
    <input type="file" ref="upload" accept="image/*" class="hideImage" @change="changeImg" />
  </div>
</template>
<script>
export default {
  data() {
    return {
      src: "",
      username: ""
    };
  },
  mounted() {
    if (sessionStorage.userInfo) {
      this.username = JSON.parse(sessionStorage.userInfo).username;
    }
    //若用户存在，请求该用户的头像信息，不存显示默认头像
    if (this.username) {
      this.$axios.post("vue/userImg", {
          username: this.username
        }).then(res => {
          console.log(res.data.type);
          //获取到个人头像,展示，否则显示默认头像
          if(!!res.data.type){
            this.src = res.data.img.replace(/public/, "http://localhost:1906");
          }else{
              this.src = require("@/assets/images/fat.jpg");
          }
        });
    }else{
      this.src = require("@/assets/images/fat.jpg");
    }
  },
  methods: {
    //点击上传头像
    uploadImg() {
      //通过ref调用input的事件
      this.$refs.upload.click();
    },
    changeImg() {
      let file = this.$refs.upload.files[0];
      console.log(file);
      //新建表单对象
      let data = new FormData();
      data.append("myImage", file);
      //发送post请求
      // processData: false,//默认情况下，通过data选项传递进来的数据，如果是一个对象(技术上讲只要不是字符串)，都会处理转化成一个查询字符串，以配合默认内容类型 "application/x-www-form-urlencoded"。如果要发送 DOM 树信息或其它不希望转换的信息，请设置为 false。
      // contentType: false,//数据编码格式不使用jquery的方式 为了避免 JQuery 对其操作，从而失去分界符，而使服务器不能正常解析文件。data:formData,
      this.$axios({
        url: "/vue/upload",
        method: "POST",
        data: data,
        contentType: false,
        processData: false
      }).then(res => {
        //  this.touxiang = res.data.pic.replace(/public/,'');   // 配置代理
        this.src = res.data.pic.replace(/public/, "http://localhost:1906"); //不需要配置代理
        // localStorage.myImage = this.src;
      });
    }
  }
};
</script>
<style scoped>
.imgWrap {
  width: 2rem;
  height: 2rem;
  border-radius: 50%;
}
.imgWrap img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
.hideImage {
  display: none;
}
</style>